// 首頁投影片中的產品卡片
<template>
    <div class="card border-0 mt-3 card-width">
      <img :src="product.imageUrl" class="card-img-top product-img" alt="..." @click="getProduct(product.id)">
      <div class="card-body index-card-body p-0  d-flex  flex-column justify-content-center">
          <div class="h5 card-title fw-bold"  @click="getProduct(product.id)">{{product.title}}</div>
          <p class="card-text cart-gray index-card-p">${{product.price}} / {{product.unit}}</p>
          <button type="button" class="btn btn-cart-green py-1 mb-2 index-card-button fw-bold" @click="$emit('add-to-cart')">Add To Cart</button>
      </div>
    </div>
</template>

<script>
export default {
  props: ['product'],
  methods: {
    // 前往商品詳細頁面
    getProduct (id) {
      this.$router.push(`/user/usersingleproduct/${id}`)
    }
  }
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.card-width{
  width: 90%;
  img{
    cursor: pointer;
  }
  .index-card-body{
    width: 100%;
      .card-title{
          font-style: normal;
          display: flex;
          min-height: 60px;
          font-size: 16px;
          margin-top: 4px;
          cursor: pointer;
          @include pad(){
            font-size: 14px;
          }
          @include iphone(){
            font-size: 24px;
          }
      }
    .index-card-p{
        font-size: 12px;
        @include pad(){
          font-size: 10px;
        }
        @include iphone(){
          font-size: 20px;
        }
    }
    .index-card-button{
        font-size: 12px;
        @include iphone(){
          font-size: 20px;
        }
    }
  }
}
</style>
