//首頁產品投影片
<template>
  <div id="myCarousel" class="carousel slide" data-bs-ride="carousel" ref="carousel">
    <div class="carousel-inner position-relative">
      <div class="carousel-item active">
        <div class="carousel-area d-flex justify-content-between">
            <div class="product-card" v-for="product in random_product_group[0]" :key="product.id">
              <IndexCard :product="product" @add-to-cart="addToCart(product.id)"></IndexCard>
            </div>
        </div>
      </div>
      <div class="carousel-item">
          <div class="carousel-area d-flex justify-content-between">
            <div class="product-card" v-for="product in random_product_group[1]" :key="product.id">
              <IndexCard :product="product" @add-to-cart="addToCart(product.id)"></IndexCard>
            </div>
          </div>
      </div>
      <div class="carousel-item">
          <div class="d-flex justify-content-between">
            <div class="product-card" v-for="product in random_product_group[2]" :key="product.id">
              <IndexCard :product="product" @add-to-cart="addToCart(product.id)"></IndexCard>
            </div>
          </div>
      </div>
      <div class="carousel-item">
            <div class="d-flex justify-content-between">
              <div class="product-card" v-for="product in random_product_group[3]" :key="product.id">
                <IndexCard :product="product" @add-to-cart="addToCart(product.id)"></IndexCard>
              </div>
            </div>
      </div>
      <div class="carousel-item">
            <div class="d-flex justify-content-between">
              <div class="product-card" v-for="product in random_product_group[4]" :key="product.id">
                <IndexCard :product="product" @add-to-cart="addToCart(product.id)"></IndexCard>
              </div>
            </div>
      </div>
    </div>
    <div class="carousel-btn-group">
      <div class="index-carousel-item">{{random_product.length}} items</div>
      <button class="index-carousel-control" type="button" data-bs-target="#myCarousel" data-bs-slide="prev"  @click="reduce_new_Carousel">
        <span class="index-carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="visually-hidden">Previous</span>
      </button>
      <button class="index-carousel-control" type="button" data-bs-target="#myCarousel" data-bs-slide="next" @click="add_new_Carousel">
        <span class="index-carousel-control-next-icon" aria-hidden="true"></span>
        <span class="visually-hidden">Next</span>
      </button>
    </div>
  </div>
</template>

<script>
import IndexCard from '../components/IndexCard.vue'
// 匯入bootstrap/carousel
import Carousel from 'bootstrap/js/dist/carousel'
export default {
  data () {
    return {
      products: {}, // 存取api取得的全部商品
      random_product: {}, // 存取隨機選取的25的商品
      random_product_group: {}, // 存取5組投影片內商品，每組中有5個商品
      Carousel: {} // 宣告carousel實體
    }
  },
  components: {
    IndexCard
  },
  methods: {
    // 加入購物車
    addToCart (id) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      // 加入讀取效果
      // this.status.loadingItem = id
      const cart = {
        product_id: id,
        // 數量預設為一
        qty: 1
      }
      // this.isLoading = true
      this.$http.post(url, { data: cart })
        .then((res) => {
          this.$pushMessageState(res, '加入購物車')
          this.emitter.emit('cart-length-change')
          // this.isLoading = false
        })
    },
    // 取得已啟用的產品
    getProducts (page = 1) {
      // this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      // axios的$http方法
      this.$http.get(api)
        .then((res) => {
          // this.isLoading = false
          if (res.data.success) {
            this.products = res.data.products
            this.pagination = res.data.pagination
            // 從this.products隨機抓取25個產品
            this.getRandom(this.products, 25)
            // 將this.random_product分5組
            this.group(this.random_product, 5)
          }
        })
    },
    // 前往商品詳細頁面
    getProduct (id) {
      this.$router.push(`/user/usersingleproduct/${id}`)
    },
    // 從陣列中隨機抓取n
    getRandom (arr, n) {
      const result = new Array(n)
      let len = arr.length
      const taken = new Array(len)
      if (n > len) {
        throw new RangeError('getRandom: more elements taken than available')
      }
      while (n--) {
        //  floor : Math.floor() 函式會回傳小於等於所給數字的最大整數
        //  random:函數 Math.random() 會回傳一個偽隨機小數(pseudo-random) 介於0到1之間
        const x = Math.floor(Math.random() * len)
        result[n] = arr[x in taken ? taken[x] : x]
        taken[x] = --len in taken ? taken[len] : len
      }
      this.random_product = result
    },
    // 將array分subGroupLength組
    group (array, subGroupLength) {
      let index = 0
      const newArray = []
      while (index < array.length) {
        newArray.push(array.slice(index, index += subGroupLength))
      }
      this.random_product_group = newArray
    }
  },
  created () {
    this.getProducts()
  },
  mounted () {
    // 建立carousel實體
    var myCarousel = document.querySelector('#myCarousel')
    this.Carousel = new Carousel(myCarousel, {
      interval: false
    })
  },
  inject: ['emitter']
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.carousel-inner{
  @include pad(){
    width: 95%;
    margin: 0 auto;
  }
}
.product-card{
  width: 19%;
  flex: 0 0 auto;
  border: 1px solid #E8E8E8;
  display: flex;
  justify-content: center;
  @include pad(){
    width: 25%;
    margin-right: 5px;
  }
  @include ipad(){
    width: 31%;
  }
  @include phone(){
    width: 48%;
  }
  @include iphone(){
    width: 98%;
  }
}
.carousel-btn-group{
  width: 120px;
  display: flex;
  justify-content:space-between;
  position: absolute;
  right: 0;
  top:-18%;
  @include pad(){
    top: -16%;
    right: 5%;
  }
  @include smallpad(){
    width: 50px;
  }
  @include iphone(){
    top: -13%;
  }
  .index-carousel-item{
    display: flex;
    align-content: center;
    @include smallpad(){
      display: none;
    }
  }
  .index-carousel-control{
      width: 12px;
      height: 22px;
      transition: opacity 0.15s ease;
      display: flex;
      z-index: 1;
      width: 15%;
      padding: 0;
      background: none;
      border: 0;
      opacity: 0.5;
    .index-carousel-control-prev-icon{
      background: url('https://nuts.com/nutshell/img/caret-left-5aa5b9dc.svg') top center no-repeat;
      width: 12px;
      height: 22px;
    }
    .index-carousel-control-next-icon{
      background: url('https://nuts.com/nutshell/img/caret-right-85ceb05d.svg') top center no-repeat;
      width: 12px;
      height: 22px;
    }
  }
}
</style>
